.app {
  text-align: center;
}

.domain-list {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.domain-item {
  display: flex;
  justify-content: space-between;
  width: 200px;
  margin: 10px 0;
}

button.sold {
  background-color: gray;
  color: white;
  cursor: not-allowed;
}

button.available {
  background-color: blue;
  color: white;
  cursor: pointer;
}
